@font-face { font-family: 'NanumBarunpen Bold'; src: url(./fonts/NanumBarunpenB.ttf) format('truetype'); }

.Home {
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;

    min-height: 100vh;
}


.Main {
    height:969px;
    width:545.06px;

    border: 2px solid red;

    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.imgBG {
    width: auto;
    height: 100%;
}

.StartButton {
    width: 389.7px;
    height: 1vh;

    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-self: center;

    position: relative;
    margin-top: -100px;
    margin-left: 50%;
    margin-right: 50%;

    color: white;
    background: #2e425c;
    padding: 24.3px 59.4px;

    font-family: 'NanumBarunpen Bold';
    line-height: 1.7;

    border: 6.3px solid #95c8e6;
    border-radius: 47.7px;
    box-shadow: 0 0 11.7px 0 #008cff;
    outline: 0;

    font-size: 2rem;
}